  data() {
    var validateNum = (rule, value, callback) => {
      if (!value) {
        return callback(new Error("方案号不能为空"));
      }
      if (value < 18) {
        callback(new Error("不能小于18"));
      }
      callback();
    };
    var validateName = (rule, value, callback) => {
      if (value === "") {
        callback(new Error("方案名称不能为空"));
      }
      callback();
    };
    var validateSelect = (rule, value, callback) => {
      if (value === "") {
        callback(new Error("方案名称不能为空"));
      }
      callback();
    };
    return {
      dialogFormVisible: false,
      innerVisible: false,
      planData: {
        plan_num: "",
        plan_name: "",
        region: "",
      },
      rules: {
        plan_num: [{ validator: validateNum, trigger: "blur" }],
        plan_name: [{ validator: validateName, trigger: "blur" }],
