  methods: {
    drawLine() {
      this.$nextTick(() => {
        this.upChartCanvas = this.$echarts.init(
          document.getElementById("upChart")
        );
        this.downChartCanvas = this.$echarts.init(
          document.getElementById("downChart")
        );
        this.upOptions = {
          title: {},
          tooltip: {
            trigger: "axis",
          },
          legend: {
            data: ["ue1", "ue2", "ue3"],
          },
          grid: {
            left: "3%",
            right: "4%",
            bottom: "3%",
            containLabel: true,
          },
          xAxis: {
            type: "category",
            boundaryGap: false,
            axisLabel: {
              show: true,
              textStyle: {
                color: "red",
              },
            },
            // data: ["周一", "周二", "周三", "周四", "周五", "周六", "周日"],
          },
          yAxis: {
            type: "value",
          },
          series: [
            {
              name: "ue1",
              type: "line",
              stack: "总量",
              data: [120, 132, 101, 134, 90, 230, 210],
            },
            {
              name: "ue2",
              type: "line",
              stack: "总量",
              data: [220, 182, 191, 234, 290, 330, 310],
            },
            {
              name: "ue3",
              type: "line",
              stack: "总量",
              data: [150, 232, 201, 154, 190, 330, 410],
            },
          ],
        };
        this.upChartCanvas.setOption(this.upOptions);
        this.downChartCanvas.setOption(this.upOptions);
      });
    },

    conectSocket() {
      const wsuri = `ws://${customPortIp.normal_ip}:${customPort.normal_port}`;
      this.websock = new WebSocket(wsuri, "lws-minimal");
      this.websock.onmessage = this.websocketonmessage;
    },

    websocketonmessage(e) {
      let arr = [];
      for (let i = 0; i < e.data.length; i++) {
        arr.push(e.data.charCodeAt(i));
      }
      this.verti = arr[0];
      this.horiz = arr[1];
      let imp_data = arr.slice(2, arr.length);
      let temp = [];
      let temp_arr = [];
      imp_data.filter((v, index) => {
        this.colorArr.filter((m, i) => {
          if (v === m.num) {
            let obj = {
              color: m.color,
              num: m.num,
              i: index,
            };
            temp.push(obj);
            temp_arr.push(obj);
            // this.myData.push(obj);
          }
        });
      });
      let real_data = [];
      for (let t = this.verti + 1; t > -1; t--) {
        for (let j = 0; j < temp_arr.length; j++) {
          if (temp_arr[j].i % this.verti === t) {
            real_data.push(temp_arr[j]);
          }
        }
      }

      console.log(JSON.stringify(real_data));
      this.myData = real_data;
      // 数据反转处理
      // this.handelData(temp);
      // console.log(this.myData, this.myData.length);
    },
    // verti:纵,ho:横
    handelData(row) {
      // 把0=>1680转为1680>0
      // console.log(row.length, this.verti);
      let arr = [];
      for (let i = row.length; i > 0; i--) {
        arr.push(row[i - 1]);
      }
      arr = arr.reverse();
      this.myData = arr;
    },
    // 切片处理
    handelSection(num) {
      this.num_index = num;
      this.dataInfo = {};
      this.dialogFormVisible = true;
    },
    async addfixedplan(validateRef) {
      let tempData = 1;
      this.$refs[validateRef].validate((valid) => {
        if (!valid) {
          tempData = tempData + 1;
        }
      });
      if (tempData === 1) {
        console.log("没问题");
      }
    },
  },
};
