      };
      // 放大、变小动画
      G6.registerNode(
        "circle-animate",
        {
          afterDraw: function afterDraw(cfg, group) {
            var shape = group.get("children")[0];
            shape.animate(
              {
                repeat: true,
                onFrame: function onFrame(ratio) {
                  var diff = ratio <= 0.5 ? ratio * 10 : (1 - ratio) * 10;
                  return {
                    r: cfg.size / 2 + diff,
                  };
                },
              },
              3000,
              "easeCubic"
            );
          },
        },
        "circle"
      );

      // 数据流动效果
      //下面这两个参数是为了设置连线上圆圈的不同颜色
      let edgeCircleColorIndex = 0;
      let edgeCircleColorArr = ["#7CD13B", "#FF0000"];

      G6.registerEdge(
        "line-running",
        {
          afterDraw(cfg, group) {
            const shape = group.get("children")[0];
            const startPoint = shape.getPoint(0);
            //创建节点之间的圆圈，并为每一个设置样式
            const circle = group.addShape("circle", {
              attrs: {
                x: startPoint.x,
                y: startPoint.y,
                fill: "#2FAEA0",
                r: 4, //圆圈大小
              },
              name: "circle-shape",
            });

            // 实现动态效果
            circle.animate(
              (ratio) => {
                const tmpPoint = shape.getPoint(ratio);
                return {
                  x: tmpPoint.x,
                  y: tmpPoint.y,
                };
              },
              {
                repeat: true, //动画是否重复
                duration: Math.random() * 5000 + 3000, //一次动画持续时长
              }
            );
          },
        },
        "line"
      );

      this.graph = new G6.Graph({
        container: "mountNode",
        width: 600,
        height: 400,
        defaultEdge: {
          style: {
            lineWidth: 1,
            stroke: "#b5b5b5",
          },
        },
      });
      this.graph.data(data);
      this.graph.render();
    },

