              :key="ind"
              style="margin-right: 0px; font-size: 20px; display: inline-block"
            >
              <div>
                <div class="main_content" :style="{ width: horiz * 20 + 'px' }">
                  <div
                    class="color_div"
                    v-for="(item, index) in va"
                    :key="index"
                    :style="{ background: va[index].color }"
                  >
                    {{ item.num }}
                  </div>
                </div>
              </div>
            </div>
          </div>
