<script>
import { customPortIp, customPort, projectAxios } from "@/plugins/config";
export default {
  data() {
    var validateNum = (rule, value, callback) => {
      if (!value) {
        return callback(new Error("方案号不能为空"));
      }
      if (value < 18) {
        callback(new Error("不能小于18"));
      }
      callback();
    };
    var validateName = (rule, value, callback) => {
      if (value === "") {
        callback(new Error("方案名称不能为空"));
      }
      callback();
    };
    var validateSelect = (rule, value, callback) => {
      if (value === "") {
        callback(new Error("方案名称不能为空"));
      }
      callback();
    };
    return {
      reviceData: [],
      handelRealData: [],
      myData: [],
      allData: [],
      colorArr: [
        {
          num: 0,
          color: "red",
        },
        {
          num: 1,
          color: "red",
        },
        {
          num: 2,
          color: "green",
        },
        {
          num: 3,
          color: "#ffcc66",
        },
        {
          num: 4,
          color: "blue",
        },
        {
          num: 5,
          color: "purple",
        },
        {
          num: 6,
          color: "#ffccff",
        },
        {
          num: 7,
          color: "#ffff00",
        },
        {
          num: 8,
          color: "#ffff33",
        },
        {
          num: 9,
          color: "#ff33ff",
        },
        {
          num: 10,
          color: "#858585",
        },
        {
          num: 11,
          color: "#0A0A0A",
        },
        {
          num: 12,
          color: "#E6A23C",
        },
        {
          num: 21,
          color: "#FFEFD5",
        },
        {
          num: 22,
          color: "#F7F7F7",
        },
        {
          num: 23,
          color: "#DBDBDB",
        },
        {
          num: 24,
          color: "#EEE9BF",
        },
        {
          num: 25,
          color: "#E0FFFF",
        },
        {
          num: 26,
          color: "#FAFAD2",
        },
      ],
      verti: 0,
      horiz: 0,
      importent_number: 0,
    };
  },
  mounted() {
    this.conectSocket();
  },

  methods: {
    conectSocket() {
      const wsuri = `ws://${customPortIp.normal_ip}:${customPort.normal_port}`;
      this.websock = new WebSocket(wsuri, "lws-minimal");
      this.websock.onmessage = this.websocketonmessage;
    },

    websocketonmessage(e) {
      let arr = [];
      for (let i = 0; i < e.data.length; i++) {
        arr.push(e.data.charCodeAt(i));
      }
      this.verti = arr[0];
      this.horiz = arr[1];
      console.log(arr);
      let imp_data = arr.slice(2, arr.length);
      let temp_arr = [];
      imp_data.filter((v, index) => {
        this.colorArr.filter((m, i) => {
          if (v === m.num) {
            let obj = {
              color: m.color,
              num: m.num,
              i: index,
            };
            temp_arr.push(obj);
          }
        });
      });
      // todo 修改横着显示的个数, 从0开始, 如果显示到5个这里是4, 如果显示20个, 这里是19
      if (this.handelRealData.length > 19) {
        this.handelRealData.splice(0, 1);
      }

      let real_data = [];
      for (let t = this.verti + 1; t > -1; t--) {
        for (let j = 0; j < temp_arr.length; j++) {
          if (temp_arr[j].i % this.verti === t) {
            real_data.push(temp_arr[j]);
          }
        }
      }

      this.handelRealData.push(real_data);
      // console.log(JSON.stringify(this.handelRealData));
    },
    handelData(row) {
      // 把0=>1680转为1680>0
      // console.log(row.length, this.verti);
      let arr = [];
      for (let i = row.length; i > 0; i--) {
        arr.push(row[i - 1]);
      }
      arr = arr.reverse();

      this.myData = arr;
    },
    // 切片处理
    handelSection(num) {
      this.num_index = num;
      this.dataInfo = {};
      this.dialogFormVisible = true;
    },
    async addfixedplan(validateRef) {
      let tempData = 1;
      this.$refs[validateRef].validate((valid) => {
        if (!valid) {
          tempData = tempData + 1;
        }
      });
      if (tempData === 1) {
        console.log("没问题");
      }
    },
  },
};
</script>

